import React, { memo } from "react";
import ReactDatePicker from "react-datepicker";
import InputMask from "react-text-mask";
import "react-datepicker/dist/react-datepicker.css";

import { ReactComponent as CalendarIcon } from "../images/icons/calendar.svg";

import { InputError } from "./InputError";

import { makeStyles } from "@material-ui/core/styles";
import {
  InputAdornment,
  OutlinedInput,
  FormControl,
  IconButton,
  Grid
} from "@material-ui/core";

export const DatePicker = memo(({ field, form, errors }) => {
  const classes = useStyles();

  const handleButtonMouseDown = event => {
    event.preventDefault();
  };

  const CustomInput = ({ onClick, onChange, value }) => (
    <FormControl
      className={classes.container}
      variant="outlined"
      margin="normal"
      size="small">
      <Grid container justify="space-between">
        <span> Birth date </span>
        <span> * </span>
      </Grid>

      <OutlinedInput
        value={value}
        placeholder="DD/MM/YYYY"
        onChange={onChange}
        endAdornment={
          <InputAdornment position="end">
            <IconButton onMouseDown={handleButtonMouseDown} onClick={onClick}>
              <CalendarIcon />
            </IconButton>
          </InputAdornment>
        }
        inputComponent={TextMaskCustom}
      />
    </FormControl>
  );

  const CustomInputRef = React.forwardRef((props, ref) => (
    <CustomInput innerRef={ref} {...props} />
  ));

  return (
    <>
      <ReactDatePicker
        dateFormat="dd/MM/y"
        selected={field.value}
        value={field.value}
        onChange={value => form.setFieldValue(field.name, value)}
        customInput={<CustomInputRef />}
      />

      {errors && <InputError value={errors} />}
    </>
  );
});

function TextMaskCustom(props) {
  const { inputRef, ...other } = props;

  return (
    <InputMask
      {...other}
      ref={ref => {
        inputRef(ref ? ref.inputElement : null);
      }}
      mask={[
        /[0-9]/,
        /[0-9]/,
        "/",
        /[0-9]/,
        /[0-9]/,
        "/",
        /[0-9]/,
        /[0-9]/,
        /[0-9]/,
        /[0-9]/
      ]}
    />
  );
}

const useStyles = makeStyles(theme => ({
  container: {
    "& span": {
      fontFamily: "Roboto",
      fontStyle: "normal",
      fontWeight: "normal",
      fontSize: "14px",
      lineHeight: "16px",
      color: "#657C9A"
    }
  }
}));
